body {
  background-color: #f4f4fd;
  padding: 50px;
}

.breed-select {
  width: 300px;
  text-align: center;
  background-color: #e7e9fc;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 5px 50px;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.cat-info {
  max-width: 1000px;
  background-color: #e7e9fc;
  border-radius: 5px;
  margin-top: 20px;
  padding: 20px;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.div-card-cat {
  gap: 20px;
  display: flex;
}

.title-card-info {
  font-size: 30px;
  font-style: italic;
  text-decoration: underline;
}

.description-card-info, .text-card-info {
  color: #212121;
}

.span-card-info {
  font-weight: bold;
}

.img-card-cat {
  border-radius: 5px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #e7e9fc;
  border-bottom-color: #212121;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

